
import { computed, defineComponent } from 'vue'
import {
    IonCard,
    IonThumbnail,
    IonItem,
    IonLabel
} from "@ionic/vue"
import img from "@/utils/Img"

export default defineComponent({
    components: {
        IonCard, 
        IonThumbnail, 
        IonItem, 
        IonLabel
    },
    props: {
        title: {
            type: String,
            required: true
        },
        icon: {
            type: String,
            required: true
        },
        description: {
            type: String,
            required: false
        },
        taskDisabled: {
            type: Boolean,
            required: false
        },
        taskCompleted: {
            type: Boolean,
            required: false
        }
    },
    setup(props) {
        const isCompleted: boolean = typeof props.taskCompleted === 'boolean' 
            ? props.taskCompleted 
            : false;

        const isDisabled: boolean = typeof props.taskDisabled === 'boolean'
            ? props.taskDisabled
            : false;

        const activeIcon = computed(() => {
            if (isDisabled) {
                return img('Disabled.svg')
            } else if (isCompleted) {
                return img('Checkmark.svg')
            } else {
                return props.icon
            }
        }) 

        return {
            activeIcon,
            isDisabled,
            isCompleted
        }
    }
})
